<template>
	<h1>Список студентів</h1>
	<table>
		<tr>
			<th>ПІБ</th>
			<th>Середній бал</th>
		</tr>
		<tr v-for="(student, index) in currentStudentsArray" :key="index">
			<td>{{ student.name }}</td>
			<td>{{student.averageScore}}</td>
		</tr>
	</table>

</template>

<script>
import { mapGetters } from 'vuex'

	export default {
		name:'StudentsList',
		props: {
			selectedValue:{
				type:String,
				default:''
			}
		},
    data(){
			return{
				// selectedValue:''
			}
		},
		computed:{
			...mapGetters(['getStudentsArray','getStudentsArrayTo5']),
			currentStudentsArray() {
      return this.selectedValue === '1' ? this.getStudentsArray : this.getStudentsArrayTo5;
    },
		}
	}
</script>

