import { createStore } from "vuex";
import { studentsArray } from "@/constants/data";
export default createStore({
  state: {
		studentsArray:'',
		studentsArrayTo5 : [],
		selectedValue:'1'
	},
  getters: {
		getStudentsArray:(state) => state.studentsArray,
		getStudentsArrayTo5:(state) => state.studentsArrayTo5,
	},
  mutations: {
		showStudentArray(state, studentsArray){
			state.studentsArray =  studentsArray
		},
		transformArrayTo5(state) {
			state.studentsArrayTo5 = state.studentsArray.map((student) => ({  ...student,
				averageScore: Math.round(student.averageScore / 2.4)}))
 
	},
  transformArrayTo12(state, getters){
		state.studentsArray = getters.getStudentsArray
		// console.log('originalStudentsArray')
		// console.log(originalStudentsArray)
	},
	setSelectedSystem(state, selectedValue) {
		state.selectedValue = selectedValue;
	},
},	 
  actions: {
		toTransformArrayTo5({commit}){
commit('transformArrayTo5', "setSelectedSystem")
		},
    toTransformArrayTo12({ commit }) {
      commit("transformArrayTo12", "setSelectedSystem");
    },
		onLoadStudentArray({commit}){
commit('showStudentArray', studentsArray)
		}
	},
  modules: {},
});
