<template>
	<div>
<select v-model="selectedValue"  @change="selectedChange">
	<option value="1">12-бальна система</option>
	<option value="2">5-бальна система</option>
</select>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
	export default {
name:'FiltersCategory',
 data(){
	return{
	selectedValue:'1'
	}
 },
 computed:{
	...mapGetters(['getStudentsArray','getStudentsArrayTo5']),

 },
 methods:{
	selectedChange() {
      const studentArray = this.selectedValue === '2' ? 'toTransformArrayTo5' : 'toTransformArrayTo12';
      this.$store.dispatch(studentArray);
    },
 }
	}
</script>

